.app-root {
  width: 200%;
  transform: scale(0.5);
  transform-origin: left top;
  padding: 1px;
  box-sizing: border-box;
  >nav {
    margin: 0.45rem 0 0 0.5rem;
    display: flex;
    align-items: center;
    h1 {
      margin: 0;
      width: 6.4rem;
      height: 0.64rem;
      color: transparent;
      background: url("./assets/title.png") no-repeat;
      background-size: 100% 100%;
      user-select: none;
    }
    ul {
      flex: 1;
      display: flex;
      align-items: center;
      margin-left: .8rem;
    }
    li {
      margin-left: .96rem;
      list-style: none;
      &:first-child {
        margin-left: 0;
      }
      a {
        display: block;
        width: 1.24rem;
        font-size: .24rem;
        padding: 0.08rem 0;
        text-align: center;
        border-bottom: 0.02rem solid transparent;
        &.active {
          border-bottom: 0.02rem solid #00fff9;
          color: #00fff9;
        }
      }
    }
    .user-info {
      display: flex;
      align-self: flex-start;
      align-items: center;
      padding-right: .4rem;
      font-size: .18rem;
      .username {
        margin-right: .1rem;
      }
      .ant-btn {
        height: auto;
        font-size: .18rem;
      }
    }
  }
}

@media screen and (max-width: 1919px) {
  .app-root {
    width: 19.2rem;
  }
}
