.page-login {
  width: 50%;
  transform: scale(2);
  transform-origin: left top;
  padding-top: .5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  h2 {
    font-size: .1rem;
    margin-bottom: .2rem;
  }
  .login-form {
    width: 400px;
    .ant-input-affix-wrapper .ant-input {
      background-color: rgba(0, 0, 0, 0.2);
      color: #fff;
    }
  }
}